@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
:root {
  /***CSS VARIABLES**/
  --font-family: 'Manrope', sans-serif;

  --gradient-text:linear-gradient(35deg, rgba(3,168,147,1) 0%, rgba(1,99,120,1) 47%, rgba(0,212,255,1) 100%);
  --gradient-bg:linear-gradient(173deg, rgba(0,140,133,1) 0%, rgba(23,100,117,1) 45%, rgba(1,96,115,1) 80%);
  
  --color-text: #fff;
  --color-bg: #040C18;



}
html {
  /*para que el fondo se adapte al tamaño de la
    pantalla*/
  height: auto;
  min-height: 100%;
}

body {
  height: 100%;
  width: 100%;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  position: absolute;
  background-color: var(--color-bg) !important;
  text-transform: uppercase;
  overflow-x:hidden !important;
  font-family: var( --font-family);
}

.header {
  margin-top: 8rem;
  margin-bottom: 2rem;
  text-align: center;
  font-size: 5vw;
  background: var(--gradient-text);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}



::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}
::-webkit-scrollbar-thumb {
  background: linear-gradient(150deg, #00a19c 18%, #05c0e6 77%);
  border: 2.9px dotted #525252;
  border-radius: 0px;
}
::-webkit-scrollbar-thumb:hover {
  background: #0078a8;
}
::-webkit-scrollbar-track {
  background: #000000;
  border: 3.1px dotted #ffffff;
  border-radius: 0px;
  box-shadow: inset 0px 0px 0px 0px #f0f0f0;
}
@media screen and (max-width: 350px) {

  .header {

    font-size: 2rem;
 
  }

  body {
 
  overflow-x:scroll !important;
  
}
}
.heroImage {
  width: 100%;
  height: 350px;
  object-fit: scale-down;
  background: linear-gradient(124deg, rgb(65, 65, 65), rgb(0, 0, 0));
  background-size: 1800% 1800%;
  animation: rainbow 5s ease-in-out infinite;
}

.graphic {
  margin-top: 20px;

  display: flex;
  justify-content: center;
  align-items: center;
}

.bottom-HeroCard {
  margin-top:10px;
  height: 150px;
}


/*******/
@keyframes rainbow {
  0% {
    background-position: 0% 82%;
  }
  50% {
    background-position: 100% 19%;
  }
  100% {
    background-position: 0% 82%;
  }
}

@media screen and (max-width: 290px) {
 

  .button {
    margin-top: 10px;
  }
}

.inputText {
  color: rgba(255, 255, 255, 0.582);
  font-size: 1rem;
}

@media screen and (max-width: 990px) {
  .inputText {
    color: rgba(255, 255, 255, 0.582);
    font-size: 0.5rem;
  }
}

.stats {
  display: flex;
  justify-content: center;
  align-items: center;
  background: var(--gradient-bg);
  flex-direction: row;
  max-width: 25rem;
  margin: 2rem auto;
}

.height-weight {
  flex-direction: column;
  color: white;
  line-height: 3rem;
  margin-top: 3rem;
}

.typeTeam
{
  font-size: 3vw;

}
@media screen and (max-width: 990px) {
  .stats {
    flex-direction: column;
  }
}

.notFound
{   
    width: 100%;
    height:600px;
    object-fit: contain;

}
.footer {
  text-align: center;
  font-size: 1rem;
  color: #fff;

}

