.stats {
  display: flex;
  justify-content: center;
  align-items: center;
  background: var(--gradient-bg);
  flex-direction: row;
  max-width: 25rem;
  margin: 2rem auto;
}

.height-weight {
  flex-direction: column;
  color: white;
  line-height: 3rem;
  margin-top: 3rem;
}

.typeTeam
{
  font-size: 3vw;

}
@media screen and (max-width: 990px) {
  .stats {
    flex-direction: column;
  }
}
